html {
  margin: 0;
  padding: 0; 
  box-sizing: border-box; 
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}   
  
*, *::before, *::after { 
  box-sizing: inherit; 
  margin: 0;
  padding: 0;
}

/* ********************** General ************************************ */


:root{
  --title-color:#120D74;
  --nav-color:#6C63FF;
  --paragraph-color:#707070;
  --link-color:#2A20D3;
  
}
body{
  line-height: 1.5;
}

p{
  color: var(--paragraph-color);
  text-align: justify;
}

/* Logo */
h1{
  font-weight: 400;
  color: #fff;

}
h1 span {
  font-weight: 500;
}

h2{
  font-weight: 500;
  color: var(--title-color);
}

h3{
  font-weight: 400;
  text-transform: uppercase;
}

img{
  max-width:100%;
}

/* ********************** Mobile ********************************** */
@media (min-width:360px){

  .home {
    width: 100%;
    height: 260px;
    padding-top:80px;
    background: -moz-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ff3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(108,99,255,1)), color-stop(100%, rgba(42,32,211,1))); /* safari4+,chrome */
    background: -webkit-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* opera 11.10+ */
    background: -ms-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ie10+ */
    background: linear-gradient(181deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* w3c */
  }

  .wrapper{
    width: 100%;
    padding: 0 15px;
    display: flex;
    justify-content: center;
    flex-direction: column;
  }

  .home-logo{
    font-size: 38px;
    margin-bottom: 30px;
    margin: 10px auto;
  }
  
  footer{
    width: 100%;
    height: 50px;
    background: var(--title-color);
    margin-top: 60px;
  }

  /* Search */

  .search-container{
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    
  }
  .search {
    width: 90%;
    height: 35px;
    position: absolute;
    margin: 0 auto;
    border-radius: 10px;
    border:none;
    background: #fff;
    padding: 10px;
    font-size: 14px;
    -moz-box-shadow: none;
    box-shadow: none;
    cursor: text;
    color:#ADADAD;
    z-index: 2;

  }
  .search:focus{
    outline: none;
  }
 
  .autocomplete{
    width: 90%;
    padding: 10px;
    border-radius: 0px 0px 10px 10px;
    background: #fff;
    font-size: 14px;
    position: absolute;
    padding-bottom: 20px;
    z-index: 3;
    top: 27px;
    -webkit-box-shadow: 13px 13px 24px -8px rgba(0,0,0,0.75);
-moz-box-shadow: 13px 13px 24px -8px rgba(0,0,0,0.75);
box-shadow: 13px 13px 24px -8px rgba(0,0,0,0.75);
  }

  .autocomplete ul{
    list-style: none;
    padding-top:5px;
  }
  .autocomplete li:not(:last-child){
    margin-bottom: 15px;
  }

  .autocomplete-link{
    text-decoration: none;
    color:#ADADAD;
    display: block;
  }
  .autocomplete-link span{
    display: block;
    font-size: 12px;
  }

  /* List Cities */

  h2{
    margin-top: 30px;
    margin-bottom: 20px;
  }

  .list-container{
    overflow-x: scroll;
    -webkit-overflow-scrolling:touch;
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none;
  }
  .list-container::-webkit-scrollbar { 
    display: none;
}
  .list-cities{
    width: fit-content;
    list-style: none;
    display: flex;
    overflow-x: scroll; 
    padding: 10px 0;
    
  }

  .list-cities li {
    width: 100px;
    height: 125px;
    padding: 10px 5px;
    display: flex;
    justify-content: center;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
  }

  .list-cities li::after{
    content: "";
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(42,32,211,1) 100%);
    border-radius: 10px;


  }
  .list-cities li:not(:last-child){
    margin-right: 20px;
  }

  .list-cities li h3{
    height: 35px;
    font-size: 10px;
    color:#fff;
    align-self:flex-end;
    text-align: center;
    z-index: 1;
  }

  .list-cities li a{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
  }

  /*Cities*/

  .city-container{
    display: grid;
    grid-template-columns: 100%;
  }
  section{
    margin-bottom: 70px;
  }

  /*Header*/
  header{
    position: sticky;
    top:0;
    z-index: 999;
    width: 100%;
    height: 60px;
    display: flex;
    background: -moz-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ff3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(108,99,255,1)), color-stop(100%, rgba(42,32,211,1))); /* safari4+,chrome */
    background: -webkit-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* opera 11.10+ */
    background: -ms-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ie10+ */
    background: linear-gradient(181deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* w3c */
  }

  
  .header-logo a{
    text-decoration: none;
    color: #fff;
  }

  .header-container{
    display: flex;
    justify-content: space-between;
  }

  .header-container .search-container{
    display: none;
  }
  .search-btn{
    margin-top: 15px;
    fill:#fff;
  }
  .close-btn{
    margin-top: 15px;
    fill:#fff;
    display: none;
    cursor: pointer;
  }
  .search-mobile-trigger{
    cursor: pointer;
  }

  .search-mobile-container{
    width: 100%;
    height: 100vh;
    background: -moz-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ff3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(108,99,255,1)), color-stop(100%, rgba(42,32,211,1))); /* safari4+,chrome */
    background: -webkit-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* opera 11.10+ */
    background: -ms-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ie10+ */
    background: linear-gradient(181deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* w3c */
    position: fixed;
    top:0;
    left: 0;
    z-index: 9999;
    display: none;
    padding: 0 10px;
    flex-direction: column;
  }

  .closebtn-container{
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }

  .search-mobile{
    width: 100%;
    margin-top: 50px;
  }
  .show{
    display: block;
  }
  .hide{
    display: none;
  }
  .flex{
    display: flex;
  }

  .spinner-container{
    width: 100%;
    height: 100vh;
    background: -moz-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ff3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(108,99,255,1)), color-stop(100%, rgba(42,32,211,1))); /* safari4+,chrome */
    background: -webkit-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* safari5.1+,chrome10+ */
    background: -o-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* opera 11.10+ */
    background: -ms-linear-gradient(269deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* ie10+ */
    background: linear-gradient(181deg, rgba(108,99,255,1) 0%, rgba(42,32,211,1) 100%); /* w3c */
    position: absolute;
    top:0;
  }


  /*Navegation*/

  .nav-container{
    height: 40px;
    position: sticky;
    top:60px;
    z-index: 999;
  }

  nav{
    line-height: 40px;
    
    background: #fff;
    overflow-x: scroll;
    -webkit-overflow-scrolling:touch;
    -ms-overflow-style: none;
    overflow: -moz-scrollbars-none;
    cursor: pointer;

  }
  nav::-webkit-scrollbar { 
    display: none;
}
  nav ul{
    width: fit-content;
    list-style: none;
    display: flex;
    overflow-x: scroll; 
  }

  nav ul li{
    text-align: center;
    width: max-content;
    font-size: 18px;
    color: var(--nav-color);
  }

  nav li .active{
    font-weight: 500;
  }

  nav ul li:not(:last-child){
    margin-right: 25px;
  }

  /*Info City*/
  .city-name{
    font-size: 30px;
    text-transform: uppercase;
    margin: 0;
  }

  .country{
    color:var(--title-color);
    margin-bottom: 20px;
    margin-top: -5px;
  }

  .city-photo{
    margin-bottom: 20px;
  }

  /* See & Do*/
  .pointsOfInterest{
    width: 100%;
    list-style: none;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 120px;
    grid-row-gap: 30px;
  }

  .pointsOfInterest li{    
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    align-self: start;
    justify-self: center;
  }
  
  .pointsOfInterest div {
    width: 100px;
    height: 125px;    
    position: relative;
    align-self: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    border-radius: 10px;
  }

  .pointsOfInterest div::after{
    content: "";
    position: absolute;
    top:0;
    width: 100%;
    height: 100%;
    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,0) 0%, rgba(42,32,211,0.3) 100%);
    border-radius: 10px;
  }

  .pointsOfInterest li h3{
    margin-top: 10px;
    font-size: 14px;
    font-weight: 400;
    text-transform:capitalize;
    color: var(--link-color);
    text-align: center;
    z-index: 1;
  }

/* ACTIVITIES*/
.activities{
  list-style: none;
}

.activities li:not(:last-child) {
  margin-bottom: 20px;
}

.activities h4{
  font-size: 16px;
  font-weight: 400;
  text-transform: capitalize;
  color: var(--link-color);
}
.activities p{
  text-align: justify;
}

/* MAP*/
.map-container{
  width: 100%;
  height: 400px;
  background: var(--link-color);
}

/* WEATHER*/

.weather-container{
  overflow-x: scroll;
  -webkit-overflow-scrolling:touch;
  -ms-overflow-style: none;
  overflow: -moz-scrollbars-none;
}
.weather-container::-webkit-scrollbar { 
  display: none;
}
.weather{
  width: fit-content;
  list-style: none;
  display: flex;
  overflow-x: scroll; 
  padding: 10px 0;
  
}

.weather li {
  width: 90px;
  height: 125px;
  padding-top: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  border-radius: 10px;
}

.celsius {
  width: 42px;
  color:var(--link-color);
}
.date{
  color:var(--title-color);
}


  


}



@media (min-width: 500px){
  .pointsOfInterest{
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 40px;
  }
}




/* ********************** de Mobile a TABLET ************************* */
